/* Smartphone */
.smartphone {
        width: 80px;
        height: 120px;
        border: 5px #e1e1e1 solid;
        border-top-width: 15px;
        border-bottom-width: 20px;
        border-radius: 4px;
        -webkit-animation: smartphone 6s ease-in-out infinite;
        animation: smartphone 6s ease-in-out infinite;
}
  
@-webkit-keyframes smartphone {
        0% {
                -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        }
        25% {
                -webkit-transform: rotate(-90deg);
                transform: rotate(-90deg);
        }
        50% {
                -webkit-transform: rotate(-180deg);
                transform: rotate(-180deg);
        }
        75% {
                -webkit-transform: rotate(-270deg);
                transform: rotate(-270deg);
        }
        100% {
                -webkit-transform: rotate(-360deg);
                transform: rotate(-360deg);
        }
}
  
@keyframes smartphone {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    25% {
      -webkit-transform: rotate(-90deg);
              transform: rotate(-90deg);
    }
    50% {
      -webkit-transform: rotate(-180deg);
              transform: rotate(-180deg);
    }
    75% {
      -webkit-transform: rotate(-270deg);
              transform: rotate(-270deg);
    }
    100% {
      -webkit-transform: rotate(-360deg);
              transform: rotate(-360deg);
    }
}
.smartphone:before {
    content: '';
    display: block;
    width: 10px;
    height: 3px;
    margin: -9px auto;
    background: #c8c8c8;
    border-radius: 4px;
}
  
.smartphone:after {
    content: '';
    display: block;
    width: 12px;
    height: 12px;
    margin: 4px auto;
    background: #c8c8c8;
    border-radius: 10px;
}
  
.smartphone .content {
    width: 60px;
    height: 100px;
    margin: 15px 0 0 0;
    padding: 10px;
    background: #9b3c6f;
    color: #762e55;
    font-size: 0.5em;
    overflow: hidden;
}
  
.smartphone ul li {
    height: 6px;
    margin: 0 0 5px 0;
    border-color: #762e55;
    background: #762e55;
}
  
.smartphone ul {
        -webkit-animation: screen 6s ease-in-out infinite;
        animation: screen 6s ease-in-out infinite;
        list-style: none;
}

@-webkit-keyframes screen {
        0% {
                -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        }
        25% {
                -webkit-transform-origin: left;
              transform-origin: left;
                -webkit-transform: rotate(90deg) translateY(-1%) translateX(-217%) scaleX(1.7);
              transform: rotate(90deg) translateY(-1%) translateX(-217%) scaleX(1.7);
        }
        50% {
                -webkit-transform-origin: unset;
                transform-origin: unset;
                -webkit-transform: rotate(180deg);
                transform: rotate(180deg);
        }
        75% {
                -webkit-transform-origin: left;
                transform-origin: left;
                -webkit-transform: rotate(270deg) translateY(-1%) translateX(50%) scaleX(1.7);
                transform: rotate(270deg) translateY(-1%) translateX(50%) scaleX(1.7);
        }
        100% {
                -webkit-transform-origin: unset;
                transform-origin: unset;
                -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
  
@keyframes screen {
        0% {
                -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        }
        25% {
                -webkit-transform-origin: left;
                transform-origin: left;
                -webkit-transform: rotate(90deg) translateY(-1%) translateX(-217%) scaleX(1.7);
                transform: rotate(90deg) translateY(-1%) translateX(-217%) scaleX(1.7);
        }
        50% {
                -webkit-transform-origin: unset;
                transform-origin: unset;
                -webkit-transform: rotate(180deg);
                transform: rotate(180deg);
        }
        75% {
                -webkit-transform-origin: left;
                transform-origin: left;
                -webkit-transform: rotate(270deg) translateY(-1%) translateX(50%) scaleX(1.7);
                transform: rotate(270deg) translateY(-1%) translateX(50%) scaleX(1.7);
        }
        100% {
                -webkit-transform-origin: unset;
                transform-origin: unset;
                -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
        }
}
  
